<template>
  <div class="TouchTip" ref="TouchTip">
    <p>{{start}}</p>
    <div
      ref="tip"
      :style="{'left': left+'px'}"
      class="tip"
      @touchstart.stop.prevent="touchstart"
      @mousedown.stop.prevent="touchstart"
    >
      <p :class="{'touch': touch}">{{data | numFilter}}</p>
    </div>

    <div v-if="touch" ref="tip" :style="{'left': left+'px', 'z-index':'-1'}" class="tip tip-z">
      <p :class="{'touch': touch}">
        <span class="left"></span>
        <span class="right"></span>
      </p>
    </div>

    <p>{{end}}</p>
  </div>
</template>

<script>
export default {
  data() {
    return {
      left: 0,
      data: 0,
      touch: false,
      device: "",
      upmethod: ""
    };
  },
  props: {
    start: {
      type: [Number, String],
      default: 0
    },
    end: {
      type: [Number, String],
      default: 100
    },
    num: {
      type: [Number, String],
      default: 20
    }
  },
  mounted() {
    this.device = this.bRedir() == "pc" ? "onmousemove" : "ontouchmove";
    this.upmethod = this.bRedir() == "pc" ? "onmouseup" : "ontouchend";

    this.dataInit(this.num, this.end);

    let TouchTipWidth = this.$refs.TouchTip.offsetWidth;
    let w = this.$refs.tip.offsetWidth;
    if (+this.num > +this.end) {
      this.left = 0;
      this.data = this.start;
    } else {
      this.left = (this.num * (TouchTipWidth - w)) / this.end;
    }
  },
  methods: {
    touchstart(e) {
      this.touch = true;
      let scrollX =
        document.documentElement.scrollLeft || document.body.scrollLeft;
      e = e || window.event;
      e = this.device == "onmousemove" ? e : e.touches ? e.touches[0] : e;
      let TouchTipLeft = this.getLeft(this.$refs.TouchTip);
      let TouchTipWidth = this.$refs.TouchTip.offsetWidth;
      let w = e.target.offsetWidth;
      let x = (e.pageX || e.clientX + scrollX) - TouchTipLeft;
      let offsetLeft = this.left;
      document[this.device] = e => {
        e = e || window.event;
        e = this.device == "onmousemove" ? e : e.touches ? e.touches[0] : e;
        let mx = e.clientX;
        this.left = mx - TouchTipLeft - x + offsetLeft;
        this.dataInit(this.left, TouchTipWidth - w);
        if (this.left <= 0) {
          this.left = 0;
          this.dataInit(0, TouchTipWidth - w);
        } else if (this.left >= TouchTipWidth - w) {
          this.left = TouchTipWidth - w;
          this.dataInit(TouchTipWidth, TouchTipWidth);
        }
      };
      document[this.upmethod] = e => {
        this.touch = false;
        document[this.device] = null;
        document[this.upmethod] = null;
      };
    },

    // 设备判断
    bRedir() {
      if (/(iPhone|iPad|iPod|iOS)/i.test(navigator.userAgent)) {
        return "ios"; //移动端
      } else if (/(Android)/i.test(navigator.userAgent)) {
        return "android"; //移动端
      } else {
        return "pc"; //PC端
      }
    },
    // 相对于指定父级的偏移
    getLeft(e, o) {
      if (!e) return 0;
      var offset = e.offsetLeft;
      if (e.offsetParent && e.offsetParent.className != o)
        offset += this.getLeft(e.offsetParent, o);
      return offset;
    },
    dataInit(start, end) {
      this.data = parseInt((start * this.end) / end);
    }
  },
  filters: {
    numFilter(val) {
      if (val < 999) {
        return val;
      } else {
        return "999+";
      }
    }
  }
};
</script>

<style>
.TouchTip {
  position: relative;
  background: #5075f8;
  width: 95%;
  margin: 0 auto;
  height: 50px;
  border-radius: 10px;
  color: #fff;
  display: flex;
  padding: 0 20px;
  box-sizing: border-box;
  justify-content: space-between;
  align-items: center;
  font-size: 15px;
}
.TouchTip .tip {
  position: absolute;
  /* background: inherit; */
  border-radius: 50%;
  width: 50px;
  height: 50px;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}
.TouchTip .tip p {
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  border: 5px solid #5075f8;
  background: #cee7f7;
  color: #365d78;
  border-radius: inherit;
  display: flex;
  align-items: center;
  justify-content: center;
  animation: openUp 0.2s 1;
  cursor: pointer;
}
.TouchTip .tip p.touch {
  animation: bounceInUp 0.8s 1;
  animation-fill-mode: forwards;
}
.TouchTip .tip-z p{
    border: 5px solid transparent;
}
.tip-z .left {
  width: 70%;
  height: 40%;
  position: absolute;
  left: -20%;
  bottom: -20%;
  overflow: hidden;
  border-radius: 30%;
  animation: tipz-h 0.8s 1;
}
.tip-z .left::before {
    content: "";
    position: absolute;
    left: -10%;
    top: -10%;
    background: transparent;
    width: 42%;
    height: 68%;
    border-radius:50%;
    box-shadow: 0 0 0 400px #5075f8;
    animation: before-after 0.8s 1;
}
.tip-z .left::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    background: #fff;
    width: 12%;
    height: 100%;
}

.tip-z .right {
  width: 70%;
  height: 40%;
  position: absolute;
  right: -20%;
  bottom: -20%;
  overflow: hidden;
  animation: tipz-h 0.8s 1;
}
.tip-z .right::before {
    content: "";
    position: absolute;
    right: -10%;
    top: -10%;
    background: transparent;
    width: 42%;
    height: 68%;
    border-radius:50%;
    box-shadow: 0 0 0 400px #5075f8;
    animation: before-after 0.8s 1;
}
.tip-z .right::after {
    content: "";
    position: absolute;
    right: 0;
    bottom: 0;
    background: #fff;
    width: 12%;
    height: 100%;
}

@keyframes tipz-h {
    0% {
        bottom: -10%;
    }
    10% {
        bottom: -20%;
    }
}

@keyframes before-after {
  from,
  60%,
  75%,
  90%,
  to {
    -webkit-animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
    animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
  }

  from {
    width: 0;
    height: 0;
  }
    
  20% {
    width: 45%;
    height: 100%;
    top: -10%;
  }

  50% {
    width: 30%;
    height: 50%;
    top: -30%;
  }

  80% {
    width: 50%;
    height: 80%;
    top: -10%;
  }

  to {
    width: 42%;
    height: 68%;
  }
}

@keyframes openUp {
  0% {
    transform: translate3d(0, -90%, 0);
  }
  100% {
    transform: translate3d(0, 0, 0);
  }
}

@keyframes bounceInUp {
  from,
  60%,
  75%,
  90%,
  to {
    -webkit-animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
    animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
  }

  from {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }

  20% {
    -webkit-transform: translate3d(0, -100%, 0);
    transform: translate3d(0, -100%, 0);
  }

  50% {
    -webkit-transform: translate3d(0, -80%, 0);
    transform: translate3d(0, -80%, 0);
  }

  80% {
    -webkit-transform: translate3d(0, -95%, 0);
    transform: translate3d(0, -95%, 0);
  }

  to {
    -webkit-transform: translate3d(0, -90%, 0);
    transform: translate3d(0, -90%, 0);
  }
}
</style>
